//------------------------------------
//    #Helpers
//------------------------------------


%horizontal-ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

%horizontal-li {
    height: 100%;
    float: left;
    margin: 0;
    padding: 0;
}




// Clearfix
//---------------------------------------------

%clearfix {
    &:after {
        content: "";
        display: table;
        clear: both;
    }
}

 
/* Screen reader text
--------------------------------------------- */

.srt {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

// for pseudo-elements
//---------------------------------------------

%pseudos {
    display: block;
    content: '';
    position: absolute;
}
